import React, { useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import "react-pdf/dist/esm/Page/AnnotationLayer.css";
import "react-pdf/dist/esm/Page/TextLayer.css";

//  PDF worker
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

function PdfViewer({ file, section, onNumPages }) {
  const [pageNumber, setPageNumber] = useState(section.start);
  const [numPages, setNumPages] = useState(null);

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
    if (onNumPages) onNumPages(numPages);
    setPageNumber(section.start);
  };

  const goPrevPage = () => {
    if (pageNumber > section.start) setPageNumber(pageNumber - 1);
  };

  const goNextPage = () => {
    if (pageNumber < section.end) setPageNumber(pageNumber + 1);
  };

  return (
    <div className="pdf-wrapper">
      <div className="pdf-controls">
        <button
          className="nav-btn"
          onClick={goPrevPage}
          disabled={pageNumber <= section.start}
        >
           
        </button>
        <div className="page-indicator">
           {pageNumber}  {numPages || ""}
        </div>
        <button
          className="nav-btn"
          onClick={goNextPage}
          disabled={pageNumber >= section.end}
        >
           
        </button>
      </div>

      <div className="pdf-viewer">
        <Document file={file} onLoadSuccess={onDocumentLoadSuccess}>
          <Page pageNumber={pageNumber} />
        </Document>
      </div>
    </div>
  );
}

export default PdfViewer;
